    function createMarkup(arr) {
    return arr.map((item) => `
    <li class="gallery-item">
        <a class="gallery-link" href="${item.original}">
            <img class="gallery-image" src="${item.preview}" data-source="${item.original}" alt="${item.description}" />
        </a>
    </li>
    `).join("");
    }





